.Home{
    margin: 0px;
    padding: 0px;
}
.movieList{
    padding-left: 40px;
    padding-right: 40px;
    display: grid;
        gap: 15px;
        grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
        margin: 10px;
        row-gap: 40px;
}
.pageNum{
    color: white;
    padding-left: 50px;
    padding-right: 50px;
    justify-content: space-between;
    font-size: 50px;
}
.pageNum ul{
justify-content: center;
}
.pageNum button{
    font-size: 20px;
    padding: 7px 17px;
    color: white;
}
@media (max-width:1440px) {
    .movieList {
            padding-left: 30px;
            padding-right: 30px;
            gap: 0px;
            row-gap: 10px;
    }
}
@media (max-width:1024px) {
    .movieList {
        padding-left: 3px;
        padding-right: 3px;
        gap: 0px;
        row-gap: 20px;
        margin: -1px;
    }
}

@media (max-width: 768px) {
    .movieList {
        padding-left: 20px;
        padding-right: 20px;
        gap: 0px;
        row-gap: 20px;
        margin: 20px;
    }
}
@media (max-width: 426px) {
    .movieList {
        padding-left: 30px;
    }
    .pageNum{
        padding-left: 2px;
        padding-right: 2px;
        font-size: 0px;
    }
        .pageNum button {
            font-size: 10px;
            padding: 2px 5px;
            color: white;
        }
        
}
@media (max-width: 376px) {
    .movieList {
        padding-left: 0px;
    }
}
