body{
    margin: 0px;
}
.Navbar{
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.303);
    background-color: rgba(0, 0, 0, 0.957);
    display: flex;
    flex-direction: row;
    align-items: center;
}
.Navbar .webHeading{
    padding-left: 20px;
    cursor: pointer;
}
.Navbar .webHeading p{
    color: #932020;
    font-size: 28px;
    font-family: Oswald;
    font-weight: bolder;
}
.Navbar .nav-items{
    list-style:none;
    display: flex;
    flex-direction: row;
    color: white;
    gap: 10px;
    font-size: 20px;
    font-family: Open Sans;
    font-weight: bolde;
}
.Navbar .nav-items li{
    cursor: pointer;
}
.Navbar .nav-items li:hover {
    color: blue;
    border-bottom: 1px solid blue;
}
.Navbar .search{
    margin-left: 185px;
    display: flex;
    flex-direction: row;
    border: 1px solid black;
}
.Navbar .search input{
    padding: 4px;
    width: 175px;
    font-size: 18px;
   outline: none; 
   border: none;
}
.Navbar .search button{
    outline: none;
    padding: 7px 15px;
    color: white;
    background-color: blue;
    font-size: 20px;
    border: none;
    cursor: pointer;
}
.Navbar .search button:hover{
    background-color: rgba(0, 0, 255, 0.744);
}
@media (max-width:1440px) {
    .Navbar .search{
        margin-left: 120px;
    }
}
@media (max-width:1024px) {
    .Navbar .nav-items{
            gap: 7px;
                font-size: 12px;
    }
          .Navbar .search {
              margin-left: 40px;
          }
}
@media (max-width:768px) {
    .Navbar .nav-items {
    display: none;
    }

    .Navbar {
        justify-content: space-between;
    }
}

@media (max-width:426px) {
    .Navbar .webHeading{
        padding-left: 5px;
    }
    .Navbar .webHeading p{
        font-size: 18px;
    }
        .Navbar .search input {
            padding: 4px;
            width: 120px;
            font-size: 12px;
        }
                .Navbar .search button {
                    padding: 4px 8px;
                    font-size: 18px;
                }
}
