.moviecard{
    border-radius: 5px;
    background-color: black;
    border: 1px solid black;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.212);
    width: 320px;
    height: 470px;
}
.moviecard img{
    width: 320px;
    height: 225px;
}
.moviecard .title {
color: red;
font-size: 20px;
}
.moviecard .item-info{
    margin-top: -20px;
    padding: 5px;

    font-weight: bolder;
}
.moviecard .releaseDate{
    font-size: 18px;
    margin-top: -15px;
    color: white;
    font-weight: bold;
        font-family: Noto Sans;
}
.moviecard .overview {
    font-size: 15px;
    margin-top: -15px;
    color: white;
        font-family: Noto Sans;
}
.moviecard .rating{
        font-size: 18px;
            margin-top: -15px;
            color: goldenrod;
            font-family: Oswald;
}
.moviecard .btn{
    display: flex;
    justify-content: space-between;
}
.moviecard .btn button{
    border-radius: 2px;
    padding: 3px 27px;
    font-size: 23px;
    align-items: center;
}
a{
    text-decoration: none;
}
.moviecard .btn a button {
    cursor: pointer;
    outline: none;
    border: none;
    display: flex;
    align-items: center;
    font-family: Oswald;
}
.watch{
    background-color: red;
    color: white;
}
.watch:hover{
    background-color: rgba(255, 0, 0, 0.652);
}
.download{
    background-color: green;
    color: white;
}
.download:hover{
    background-color: rgba(0, 128, 0, 0.615);
}